
@media (max-width: 600px) {
.hide-width {
    display: none !important;
}
}
@media (min-width: 601px) and (max-width: 992px) {
.hide-tablet {
    display: none !important;
}
}
@media (min-width: 993px) {
.hide-desktop {
    display: none !important;
}
}
.message .container .p-main {
  margin: .3rem .4rem;
  box-shadow: 0 0 4px 2px rgba(0, 0, 0, 0.2);
  border-radius: 6px;
  padding: .3rem;
  font-size: .3rem;
}
.message .container .p-main .title {
  font-size: 1.3em;
  font-weight: 700;
  margin-top: .2rem;
  margin-bottom: .4rem;
}
.message .container .p-main .content > div {
  margin: .22rem 0 .1rem;
  line-height: 1.6em;
}



